html {
  --blue: #00acdc;
}

body, ul, a {
  margin: 0;
  padding: 0;
}

a {
  color: #000;
  text-decoration: none;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  display: flex;
  position: relative;
}

.logo__link {
  height: 40px;
}

.nav__ul {
  list-style: none;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin-left: 20px;
  font-size: 12px;
  font-weight: 500;
  display: flex;
}

.nav__item:first-child {
  margin-left: 0;
}

.is-active {
  color: var(--blue);
  align-items: center;
  height: 100%;
  display: flex;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.nav__item:first-child, .nav__link:hover {
  color: var(--blue);
}

